import { Component, OnInit } from '@angular/core';
import { DocumentViewerService } from './document-viewer.service';

@Component({
  selector: 'app-document-viewer',
  templateUrl: './document-viewer.component.html',
  styleUrls: ['./document-viewer.component.scss']
})
export class DocumentViewerComponent implements OnInit {

  reports: any[] = [];
  isLoading: boolean = true;
  reportDocuments: any[] = [];
  showDocsTable: boolean = false;

  constructor(private _docService: DocumentViewerService) { }

  ngOnInit() {
    this.getReports();
  }

  getReports(): void {
    this.isLoading = true;
    this._docService.getDocumentReports().subscribe((res: any) => {
      this.reports = res.feederReports;
      this.isLoading = false;
    });
  }

  onFolderSelect(selectedFolder: any): void {
    this.reportDocuments = this.reports.filter(x => x.folderName === selectedFolder.folderName)[0].files;
    this.showDocsTable = true;
  }

  showFolderTable(): void {
    this.showDocsTable = false;
  }

  downloadFile(item): void {
    this._docService.getFile('assets/data/Reports/Bharath Konaganti.doc').subscribe(res => {
      const blob = new Blob(res);
    });
  }

}



